<style>@font-face {
  font-family: 'majormono';
  src: url("../../public/MajorMonoDisplay-Regular.ttf") format("truetype"); }

.pauseButton {
  background: #006A4E; }

.paused {
  background: #D21034; }

/*# sourceMappingURL=PauseButton.svelte.css.map */</style>

<script>
	import classNames from 'classnames';
	import { pauseStatus } from '../stores';

	const pause = () => {
		pauseStatus.update(paused => !paused);
	};
</script>

<section class={classNames('pauseButton', { paused: $pauseStatus })} >
	<h1 on:click={pause}>{$pauseStatus ? "Start reactor" : "Turn off reactor"}</h1>
</section>
